import React, { useEffect, useRef } from "react";
import styled from "styled-components";
import FacebookIcon from "@material-ui/icons/Facebook";
import GithubIcon from "@material-ui/icons/GitHub";
import LikendInIcon from "@material-ui/icons/LinkedIn";
import Particle from "../Components/Particle";
import { init } from "ityped";
import "animate.css/animate.min.css";

function HomePage() {
  const textRef = useRef();
  useEffect(() => {
    init(textRef.current, {
      showCursor: true,
      cursorChar: "|",
      backSpeed: 100,
      backDelay: 1500,
      strings: ["Developer!", "Designer!"],
    });
  }, []);

  return (
    <HomePageStyled>
      <div className="particle-con">
        <Particle />
      </div>
      <div className="typography">
        <h1 className="animate__animated animate__backInDown">
          Hi, I'm <span>Daniel Olivero</span>
        </h1>
        <div className="container animate__animated animate__backInDown">
          <p>
            Frelancer <span ref={textRef}></span>
          </p>
        </div>

        <div className="icons animate__animated animate__backInDown">
          <a
            href="https://www.facebook.com/yorgenisdaniel/"
            className="icon i-facebook"
            target="_blank"
            rel="noreferrer"
          >
            <FacebookIcon />
          </a>
          <a
            href="https://github.com/Y-Daniel-24"
            target="_blank"
            className="icon i-github"
            rel="noreferrer"
          >
            <GithubIcon />
          </a>
          <a
            href="https://codepen.io/pen/"
            className="icon i-LinkedIn"
            target="_blank"
            rel="noreferrer"
          >
            <LikendInIcon />
          </a>
        </div>
      </div>
    </HomePageStyled>
  );
}

const HomePageStyled = styled.header`
  width: 100%;
  height: 100vh;
  position: relative;

  .typography {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    .container {
      display: flex;
      align-item: center;
      justify-content: justify;
      p {
        text-align: right;
        font-size: 30px;
        @media screen and (max-width: 500px) {
          font-size: 25px;
        }
        span {
          font-size: 25px;
        }
      }
    }

    .icons {
      display: flex;
      margin-top: 1rem;
      .icon {
        border: 2px solid var(--border-color);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all 0.4s ease-in-out;
        cursor: pointer;
        &:hover {
          border: 2px solid var(--primary-color);
          color: var(--primary-color);
        }
        &:not(:last-child) {
          margin-right: 1rem;
        }
        svg {
          margin: 0.5rem;
        }
      }

      .i-LinkedIn {
        &:hover {
          color: var(--primary-color);
        }
      }
      .i-github {
        &:hover {
          border: 2px solid #5f4687;
          color: #5f4687;
        }
      }
    }
  }
`;

export default HomePage;
