/* eslint-disable no-unused-vars */
import { useState } from "react";
import { useEffect } from "react";
import Sidebar from "./Components/Sidebar";
import styled from "styled-components";
import HomePage from "./Pages/HomePage";
import AboutPage from "./Pages/AboutPage";
import PortfoliosPage from "./Pages/PortfoliosPage";
import ContactPage from "./Pages/ContactPage";
import Brightness4Icon from "@material-ui/icons/Brightness4";
import ArrowDropUpSharpIcon from "@material-ui/icons/ArrowDropUpSharp";
import MenuIcon from "@material-ui/icons/Menu";
import { Route, Switch as Switching } from "react-router";
import Switch from "@material-ui/core/Switch";
import { IconButton } from "@material-ui/core";
import "animate.css/animate.min.css";
import Preloader from "./Components/Preloader";

function App() {
  const [theme, setTheme] = useState("dark-theme");
  const [checked, setChecked] = useState(false);
  const [navToggle, setNavToggle] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [scroll, setScroll] = useState(0);

  useEffect(() => {
    document.documentElement.className = theme;
  }, [theme]);

  const themeToggler = () => {
    if (theme === "light-theme") {
      setTheme("dark-theme");
      setChecked(false);
    } else {
      setTheme("light-theme");
      setChecked(true);
    }
  };
  useEffect(() => {
    document.addEventListener("scroll", () => {
      setScroll(() => {
        return document.documentElement.scrollTop;
      });
    });
  });

  // Preloader principal

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch("./Pages/HomePage")
      .then((response) => response.json())
      .then((json) => {
        setTimeout(() => {
          setLoading(false);
        }, 2000);
      })
      .catch((err) => {
        setTimeout(() => {
          setLoading(false);
        }, 2000);
      });
  }, []);

  return (
    <MainContentStyled>
      <div className="App">
        {loading ? (
          <div className="preloader">
            <Preloader />
          </div>
        ) : (
          <div>
            <Sidebar navToggle={navToggle} setNavToggle={setNavToggle} />
            <div id="title"></div>
            <div className="theme">
              <div className="light-dark-mode">
                <div className="left-content">
                  <Brightness4Icon />
                </div>
                <div className="right-content">
                  <Switch
                    value=""
                    checked={checked}
                    inputProps={{ "aria-label": "" }}
                    size="medium"
                    onClick={themeToggler}
                  />
                </div>
              </div>
            </div>
            <div
              className="arrow"
              style={scroll > 200 ? { display: "inline" } : { display: "none" }}
            >
              <div className="arrowUp">
                <a href="#title">
                  <ArrowDropUpSharpIcon />
                </a>
              </div>
            </div>
            <div
              className={
                navToggle ? `ham-burger-menu ham-translate` : "ham-burger-menu"
              }
            >
              <div onClick={() => setNavToggle(!navToggle)}>
                <div className={navToggle ? `hamburger active` : "hamburger"}>
                  <span></span>
                  <span></span>
                  <span></span>
                </div>
                {/* <MenuIcon /> */}
              </div>
            </div>
            <main>
              <div className="lines">
                <div className="line-1"></div>
                <div className="line-2"></div>
                <div className="line-3"></div>
                <div className="line-4"></div>
              </div>

              <Switching>
                <Route path="/" exact>
                  <HomePage />
                </Route>
                <Route path="/about" exact>
                  <AboutPage />
                </Route>
                {/* <Route path="/resume" exact>
            <ResumePage />
          </Route> */}
                <Route path="/portfolios" exact>
                  <PortfoliosPage />
                </Route>
                {/* <Route path="/blogs" exact>
            <BlogsPage />
          </Route> */}
                <Route path="/contact" exact>
                  <ContactPage />
                </Route>
              </Switching>
            </main>
          </div>
        )}
      </div>
    </MainContentStyled>
  );
}

const MainContentStyled = styled.main`
  .preloader {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .ham-burger-menu {
    ${"" /* position: fixed; */}
    .hamburger {
      span {
        display: block;
        width: 33px;
        height: 4px;
        margin-bottom: 5px;
        position: relative;
        background: #cdcdcd;
        border-radius: 3px;
        transform-origin: 4px 0px;
        transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
          background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
      }
    }
    .active {
      span {
        background: #000;
      }
    }
  }

  main {
    position: relative;
    margin-left: 16.3rem;
    min-height: 100vh;
    scroll-behavior: smooth;
    @media screen and (max-width: 1200px) {
      margin-left: 0;
    }

    .lines {
      position: absolute;
      min-height: 100%;
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      opacity: 0.4;
      z-index: -1;
      .line-1,
      .line-2,
      .line-3,
      .line-4 {
        width: 1px;
        min-height: 100vh;
        background-color: var(--border-color);
      }
    }
  }
`;

export default App;
