/* eslint-disable react/jsx-no-target-blank */
import React from "react";
import styled from "styled-components";

function ReviewItem({ text, text2, Link }) {
  return (
    <RevivewItemStyled>
      <div className="firtsContainer">
        <h3>{text}</h3>
        <p>{text2}</p>
      </div>
      <a href={Link} target="_blank">
        view profile
      </a>
    </RevivewItemStyled>
  );
}

const RevivewItemStyled = styled.div`
  padding: 2rem 1rem;
  border-left: 6px solid var(--border-color);
  background-color: var(--background-dark-grey);
  position: relative;
  width: 100%;
  &:not(:first-child) {
  }
  &::after {
    content: "";
    position: absolute;
    left: 2rem;
    border-width: 0.8rem;
    top: 100%;
    border-style: solid;
    border-color: var(--background-dark-grey) transparent transparent
      var(--background-dark-grey);
  }
  .firtsContainer {
    ${"" /* background: var(--scrollbar-thump-color); */}
  }
  p {
    padding: 1rem 0;
  }
  a {
    color: var(--white-color);
    cursor: pointer;
    transition: border 0.3s ease;
  }
  a:hover {
    border-bottom: 2px solid var(--font-light-color);
    transition: border 0.3s ease;
  }
`;

export default ReviewItem;
