/* eslint-disable jsx-a11y/img-redundant-alt */
/* eslint-disable react/jsx-no-target-blank */
import React, { useRef, useEffect } from "react";
import styled from "styled-components";

import PrimaryButton from "./PrimaryButton";
import { init } from "ityped";

function ImageSection({ picture }) {
  const textRef = useRef();
  useEffect(() => {
    init(textRef.current, {
      showCursor: true,
      cursorChar: "|",
      backSpeed: 100,
      backDelay: 1500,
      strings: ["Developer!", "Designer!"],
    });
  }, []);

  let fecha = new Date().getFullYear();
  let month = new Date().getMonth();

  return (
    <ImageSectionStyled>
      <div className="left-content">
        <img src={picture} alt="picture" />
      </div>
      <div className="right-content">
        <h4>
          I am <span>Daniel Olivero</span>
        </h4>
        <p className="paragraph">
          I have been in the web development business for more than 3 years, my
          experience has been based on the collaboration of talented people,
          private companies and among others to create digital products for
          commercial and consumer use. I like to stay updated with new
          technologies, with the confidence that I can achieve it.
        </p>
        <div className="about-info">
          <div className="info-title">
            <p>Full Name</p>
            <p>Age</p>
            <p>Nationality </p>
            <p>Languages </p>
            <p>Location</p>
            <p>Service</p>
          </div>
          <div className="info">
            <p>: Yorgenis D. Olivero Angarita</p>
            <p>: {month > 3 ? fecha - 2000 : fecha - 2000 - 1}</p>
            <p>: Venezuelan </p>
            <p>: Spanish, English </p>
            <p>: Aragua,Venezuela</p>
            <p>
              : <span ref={textRef}></span>
            </p>
          </div>
        </div>
        <a
          href="https://drive.google.com/file/d/1i4vF8KMya--ZMtDUToBrxLHrRVvD_2EZ/view?usp=sharing"
          target="_blank"
        >
          <PrimaryButton title={"Download Cv"} />
        </a>
      </div>
    </ImageSectionStyled>
  );
}

const ImageSectionStyled = styled.div`
  margin-top: 5rem;
  display: flex;
  @media screen and (max-width: 500px) {
    margin-top: 10px;
  }
  @media screen and (max-width: 1000px) {
    flex-direction: column;
    .left-content {
      margin-bottom: 2rem;
    }
  }
  .left-content {
    z-index: 3;
    width: 100%;
    margin-right: 15px;
    img {
      margin-top: 10px;
      width: 100%;
      border-radius: 0%;
      border: 8px solid var(--border-color);
    }
  }
  .right-content {
    width: 100%;
    h4 {
      font-size: 2rem;
      color: var(--white-color);
      span {
        font-size: 2rem;
      }
    }
    .paragraph {
      padding: 1rem 0;
      text-align: justify;
      @media screen and (max-width: 500px) {
        margin: 10px;
      }
    }
    .about-info {
      display: flex;
      @media screen and (max-width: 500px) {
        margin: 10px;
      }
      .info-title {
        padding-right: 3rem;
        p {
          font-weight: 600;
        }
        @media screen and (max-width: 446px) {
          p:nth-child(1) {
            margin-bottom: 30px;
          }
        }
      }
      .info-title,
      .info {
        p {
          padding: 0.3rem 0;
        }
      }
    }
  }
`;
export default ImageSection;
