/* eslint-disable react/jsx-no-target-blank */
import React, { Fragment, useEffect, useState } from "react";
import styled from "styled-components";
import GitHub from "@material-ui/icons/GitHub";
import Web from "@material-ui/icons/Web";
import Preloader from "./Preloader";

function Menu({ menuItem }) {
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
  }, []);
  return (
    <MenuItemStyled>
      {loading ? (
        <Fragment>
          {menuItem.map(
            ({
              id,
              image,
              link1,
              link2,
              title,
              text,
              category,
              exists,
              github,
            }) => {
              return (
                <div className="grid-item " key={id}>
                  <div className="portfolio-content">
                    <div className="portfolio-image">
                      <img src={image} alt="" className={category} />
                      {github === true || exists === true ? (
                        <ul>
                          {github ? (
                            <li>
                              <a href={link1} target="_blank">
                                <GitHub />
                              </a>
                            </li>
                          ) : (
                            <div />
                          )}

                          {exists ? (
                            <li className="pinterest">
                              <a href={link2} target="_blank">
                                <Web />
                              </a>
                            </li>
                          ) : (
                            <div></div>
                          )}
                        </ul>
                      ) : (
                        <ul></ul>
                      )}
                    </div>
                    <h6>{title}</h6>
                    <p>{text}</p>
                  </div>
                </div>
              );
            }
          )}
        </Fragment>
      ) : (
        <Preloader />
      )}
    </MenuItemStyled>
  );
}

const MenuItemStyled = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
  @media screen and (max-width: 920px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 670px) {
    grid-template-columns: repeat(1, 1fr);
    text-align:center;
  }
  .grid-item {
    .portfolio-content {
      display: block;
      position: relative;
      overflow: hidden;
      h6 {
        font-size: 1.5rem;
      }
      .portfolio-image {
        width: 250px;
        height: 200px;
        background: #fff;
        border: 5px solid var(--border-color);
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
          @media screen and (max-width:670px){
          margin:auto;
          }
      .CSS {
        width: 100%;
        object-fit: contain;
      }
      .JS {
        width: 100%;
        object-fit: contain;
      }
      .Logos {
        width: 90%;
      }
      .React {
        width: 100%;
      }
    }

    ul {
      transform: translateY(-600px);
      transition: all 0.4s ease-in-out;
      position: absolute;
      left: 50%;
      top: 40%;
      opacity: 0;
      overflow: hidden;
      li {
        background-color: var(--border-color);
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1rem;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        margin: 0 0.5rem;
        transition: all 0.4s ease-in-out;
        &:hover {
          background-color: var(--primary-color); 
        }
        a {
          display: flex;
          align-items: center;
          justify-content: center;
          ${"" /* transition: all 0.4s ease-in-out; */}
        }
      }
    }

    .portfolio-image {
        &::before {
          content: "";
          position: absolute;
          left: 0%;
          top: 0%;
          height: 0px;
          width: 0px;
          ${"" /* transition: all 0.4s ease-in-out; */}
          overflow:hidden;
        }
      }
      .portfolio-image:hover {
        position:relative;
        ul {
          transform: translateY(0);
          transform: translate(-50%, -10%);
          display: flex;
          align-items: center;
          justify-content: center;
          ${"" /* transition: all 0.4s ease-in-out; */}
          opacity: 1;
          li {
            transition: all 0.4s ease-in-out;
            &:hover {
               background-color: var(--primary-color);  
            }
            a {
              display: flex;
              align-items: center;
              justify-content: center;
              transition: all 0.4s ease-in-out;
            }
          }
          .pinterest:hover{
                background-color:red;
              }

          li:hover {
            svg {
              color: var(--white-color);
            }
          }
          svg {
            font-size: 2rem;
          }
        }
        &::before {
          height: 100%;
          width: 100%;
          background-color: var(--scrollbar-thump-color);
          opacity: 0.9;
          transform-origin: left;

          transition: all 0.4s ease-in-out;
        }
      }
    } 
    }
  }
`;

export default Menu;
